import * as React from 'react';
import styles from './PerformanceGraph.module.scss';

import { IPerfItem } from '../../../../Stores/PerformanceDisplayStore';
import PerformanceGraphItem from './PerformanceGraphItem/PerformanceGraphItem';

export interface IPerformanceGraphProps {
  /**
   * Performance data items to be shown on the graph
   */
  perfItems: IPerfItem[];
  /**
   * Integer value representing the time in ms that the page started loading
   */
  startTime: number;
  /**
   * Integer value representing how long the page took to load overall. Used for calculations
   */
  overallDuration: number;
}

export default class PerformanceGraph extends React.Component<IPerformanceGraphProps, void> {
  private _scale: number;

  constructor(props: IPerformanceGraphProps) {
    super(props);

    this._scale = Math.ceil(this.props.overallDuration / 100) * 100;
  }

  public render(): React.ReactElement<IPerformanceGraphProps> {
    const graphBars: React.ReactElement<{}>[] = [];

    this.props.perfItems.map((item: IPerfItem) => {
      graphBars.push(
        <PerformanceGraphItem
          key={ item.id }
          perfItem={ item }
          startTime={ this.props.startTime }
          overallDuration={ this._scale }
        />
      );
    });

    /**
     * Horizontal lines on the graph that show the scale
     */
    const delimiters: React.ReactElement<{}>[] = [];
    for (let i: number = 1; i <= 10; i++) {
      delimiters.push(
        <th key={ i } className={ styles.thDelimiter }>{ i * (this._scale / 10) }</th>
      );
    }

    const nameStyle: string = [
      styles.tableRowPerfList,
      styles.tableRowRightLight,
      styles.tableRowName
    ].join(' ');

    const durationStyle: string = [styles.tableRowPerfList, styles.tableRowDuration].join(' ');

    return (
      <div className={ styles.container }>
        <table className={ styles.tableDelimiter }>
          <thead>
            <tr>
              <th className={ nameStyle }>Name</th>
              <th className={ durationStyle }>Duration</th>
              { delimiters }
            </tr>
          </thead>
          <tbody>
              { graphBars }
          </tbody>
        </table>
      </div>
    );
  }
}